import React, { useState, useMemo, useCallback } from 'react';
import { Modal } from 'react-bootstrap';
import './galeria.css';

// Componente memoizado para items de galería
const GalleryItem = React.memo(({ img, index, onClick }) => {
  const [loaded, setLoaded] = useState(false);

  return (
    <div className={`gallery-item ${loaded ? 'visible' : 'loading'}`}>
      <img
        src={img}
        alt={`Imagen ${index + 1}`}
        className="gallery-img"
        loading="lazy"
        width="300"
        height="200"
        onLoad={() => setLoaded(true)}
      />
      {loaded && (
        <div className="gallery-hover" onClick={() => onClick(img)}>
          <i className="bi bi-zoom-in"></i>
        </div>
      )}
    </div>
  );
});

function Galeria() {
  const [activeGallery, setActiveGallery] = useState(1);
  const [showModal, setShowModal] = useState(false);
  const [selectedImage, setSelectedImage] = useState('');
  const [loadedImages, setLoadedImages] = useState(8); // Para paginación futura si querés

  const galleryBase = `${process.env.PUBLIC_URL}/img/Galeria`;

  // Memoización de imágenes por galería para evitar recarga
  const galleries = useMemo(() => ({
    1: Array.from({ length: 8 }, (_, i) => `${galleryBase}/galery${1 + i}.webp`),
    2: Array.from({ length: 8 }, (_, i) => `${galleryBase}/galery${9 + i}.webp`),
    3: Array.from({ length: 8 }, (_, i) => `${galleryBase}/galery${17 + i}.webp`)
  }), [galleryBase]);

  const handleImageClick = useCallback((imgSrc) => {
    setSelectedImage(imgSrc);
    setShowModal(true);
  }, []);

  const changeGallery = useCallback((id) => {
    setActiveGallery(id);
    setLoadedImages(8);
  }, []);

  return (
    <section className="gallery-section bg-dark">
      <div className="container py-3 pt-4" /* shadow-lg */>
        <div className="section-header text-center mb-4">
          <h2 className="section-title text-light">Galería de Fotos</h2>
          <p className="section-subtitle ">
            Momentos especiales de nuestra congregación
          </p>
        </div>

        <div className="gallery-grid">
          {galleries[activeGallery].slice(0, loadedImages).map((img, index) => (
            <GalleryItem
              key={index}
              img={img}
              index={index}
              onClick={handleImageClick}
            />
          ))}
        </div>

        {/* Controles de galería */}
        <div className="gallery-controls mt-3 d-flex justify-content-center">
          {Object.keys(galleries).map((key) => (
            <button
              key={key}
              className={`gallery-control-btn mx-2 ${parseInt(key) === activeGallery ? 'active' : ''}`}
              onClick={() => changeGallery(parseInt(key))}
              aria-label={`Galería ${key}`}
            >
              <span className="gallery-control-dot"></span>
            </button>
          ))}
        </div>

        {/* Link a Instagram */}
        <div className="w-100 d-flex mt-3">
          <a
            href="https://www.instagram.com/iglesia.piedrasvivas"
            className="btn btn-instagram mx-auto"
            target="_blank"
            rel="noopener noreferrer"
          >
            Ver más en Instagram
          </a>
        </div>

        {/* Modal optimizado */}
        <Modal
          show={showModal}
          onHide={() => setShowModal(false)}
          centered
          size="lg"
          className="gallery-modal"
        >
          <Modal.Body className="p-0 position-relative">
            <img
              src={selectedImage}
              alt="Imagen ampliada"
              className="img-fluid w-100"
              loading="eager"
            />
            <button
              className="modal-close-btn"
              aria-label="Cerrar modal"
              onClick={() => setShowModal(false)}
            >
              <i className="bi bi-x"></i>
            </button>
          </Modal.Body>
        </Modal>
      </div>
    </section>
  );
}

export default React.memo(Galeria);
